.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1200px;
/*    width: auto;*/
    grid-template-areas: 'h h' 'n c';

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 2px;
}

.app-wrapper-content {
  grid-area: c;
  background-color: #b9c4cc;
  color: black;

}






